.container {
  width: 100%;
  max-width: 1255px;
  margin: 0 auto; }

.button {
  background-color: inherit;
  border: none; }

@font-face {
  font-family: "Roboto Regular";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Medium";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Bold";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

body {
  margin: 0;
  font-family: "Roboto Regular", "Arial", sans-serif;
  min-width: 320px;
  position: relative; }

img {
  max-width: 100%; }

a {
  text-decoration: none;
  color: inherit; }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  background-color: #ffffff;
  font-size: 16px;
  line-height: 19px;
  padding: 25px 15px; }

.page-header__container {
  display: flex;
  align-items: center; }

.page-header__logo {
  align-self: end;
  margin-right: 43px; }

.page-header__nav {
  display: flex;
  justify-content: flex-start; }

.page-header__item {
  padding: 10px 13px;
  cursor: pointer; }
  .page-header__item:hover {
    opacity: 0.7; }
  .page-header__item:active {
    opacity: 0.3; }

.page-header__login {
  margin-left: auto;
  margin-right: 37px;
  cursor: pointer; }
  .page-header__login:hover {
    opacity: 0.7; }
  .page-header__login:active {
    opacity: 0.3; }
  .page-header__login:focus {
    outline: none; }

.promo-slider {
  height: 400px;
  width: 100%;
  background: #2c36f2 linear-gradient(180.65deg, rgba(32, 39, 179, 0) 36.31%, #2027B3 99.79%); }
